import React from 'react';
import {Bar} from 'react-chartjs-2';

const BarChart = (props) =>{
    return(<Bar
        data={
            {
                labels: props.labels,
                datasets: [{
                    label: 'Диаграмма итогов',
                    data: props.value,
                    backgroundColor: [
                        'rgba(255, 99, 132, 0.2)',
                        'rgba(255, 159, 64, 0.2)',
                        'rgba(255, 205, 86, 0.2)',
                        'rgba(75, 192, 192, 0.2)',
                        'rgba(54, 162, 235, 0.2)'
                    ],
                    borderColor: [
                        'rgb(255, 99, 132)',
                        'rgb(255, 159, 64)',
                        'rgb(255, 205, 86)',
                        'rgb(75, 192, 192)',
                        'rgb(54, 162, 235)'
                    ],
                    borderWidth: 1,
                    hoverOffset: 4
                }]
            }
        }
        options={
            
            {
                maintainAspectRatio: false,
            }
        }
    />)
}

export default BarChart;